<template>
  <div id="scoll_box">
    <div class="title">{{ $route.query.index+1 }}、{{ $route.query.title }}</div>
    <div
      v-for="(item,i) in picArr"
      :key="i"
    >
      <img
        class="img_style"
        :src="item"
      >
      <div style="height:20px;background:#eee;"></div>
    </div>
  </div>
</template>
<script>
export default {
    data() {
        return {
            picArr: {}
        };
    },
    created() {
        this.picArr = this.$route.query.picArr;
        this.$store.commit('setHeader', '问题详情');
    },
    methods: {}
};
</script>
<style scoped>
#scoll_box {
    position: fixed;
    top: 40px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    margin-top: 10px;
    font-size: 14px;
    color: #9b9b9b;
    background: #fff;
}
.title {
    text-align: left;
    color: #000;
    font-weight: bold;
    margin: 10px 10px;
}
.img_style {
    width: 98%;
    height: 80%;
}
</style>


